





















































































.article-detail {
    min-height: calc(100vh - 63px);
    padding-top: 50px;
    padding-bottom: 30px;

    .content-html {
        margin-top: 40px;
        margin-bottom: 20px;
        width: 100%;
        overflow: hidden;
    }

    .page {
        display: flex;
        justify-content: space-between;
        border-top: 1px dashed #eee;
        font-size: 12px;
        padding-top: 15px;
        margin-top: 25px;

        a {
            color: #666;

            &:hover {
                color: #333;

                i {
                    color: #333;
                }
            }
        }

        i {
            font-size: 12px;
        }
    }
}
