







































































































































































































































.comment-wrap {
    margin: 40px 0 0;

    .title {
        text-align: center;
        color: #333;
        font-weight: 500;
        font-size: 18px;
        margin-bottom: 20px;

        span {
            border-bottom: 2px solid #999;
        }
    }


    .comment-item {
        padding: 10px 0;
        color: #333;

        .parent {
            display: flex;
        }

        .avatar {
            flex: 0 0 40px;
            width: 40px;
            height: 40px;
            border-radius: 2px;
            overflow: hidden;
            margin-right: 10px;
            border: 1px solid #ddd;
        }

        .item-wrap-content {
            position: relative;
            flex: 1;
            border: 1px solid #ccc;
            border-radius: 4px;

            &:after {
                position: absolute;
                top: 11px;
                right: 100%;
                left: -16px;
                display: block;
                width: 0;
                height: 0;
                pointer-events: none;
                content: " ";
                border-color: transparent;
                border-style: solid solid outset;
                border-width: 8px;
                border-right-color: #f6f8fa;
            }

            &:before {
                position: absolute;
                top: 13px;
                left: -6px;
                display: block;
                z-index: 2;
                width: 9px;
                height: 9px;
                pointer-events: none;
                content: " ";
                border: 1px solid transparent;
                border-top-color: #ccc;
                border-left-color: #ccc;
                transform: rotate(-45deg);
            }

        }

        .iw-header {
            background: #f6f8fa;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 10px;
            border-bottom: 1px solid #ccc;
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;

            .username {
                font-size: 12px;

                span {
                    margin-right: 10px;
                }
            }
        }

        .content {
            padding: 8px 10px;
            background-color: #fff;
            border-radius: 4px;
            font-size: 14px;
        }

        .operate {
            font-size: 12px;
            line-height: 1;
            color: #666;

            span {
                margin: 0 5px;
            }

            .del, .hf {
                margin-left: 1em;
                cursor: pointer;

                &:hover {
                    color: #ff0068;
                }
            }
        }
    }

    .more {
        margin: 8px auto 20px;

        button {
            border: none;
            text-decoration: underline;
        }
    }

    .children-comment-list {
        display: block;
        margin: 10px 0 0 50px;

        .comment-rich {
            background: transparent;
            padding: 10px 0;
            margin: 0;
        }
    }
}
