






























































































































































.comment-rich {
    padding: 15px 20px 10px;
    margin-bottom: 10px;
    background-color: #f7f7f7;
    border-radius: 3px;

    textarea, .markdown-wrap {
        resize: vertical;
        max-height: 80vh;
        min-height: 100px;
        overflow-y: auto;
        border-radius: 3px;
    }

    .markdown-wrap {
        border: 1px solid #ccc;
        background: #fff;
        padding: 5px 15px;
    }

    .operate {
        padding-left: 0;
        margin-top: 15px;
        display: flex;
        justify-content: space-between;

        .btn {
            padding: 6px 20px;
            letter-spacing: 5px;
        }

        .left {
            display: flex;
            align-items: center;
            flex: 1;
            overflow-x: auto;
            margin-right: 10px;

            .icon {
                cursor: pointer;
                padding: 0 10px;
                color: #666;

                i {
                    font-size: 18px;
                }

                &:hover {
                    color: #000;
                }
            }
        }
    }
}
